  font-weight: 700;
}

@font-face {
  font-family: Titan One;
  src: local(Titan One), url("TitanOne-Regular.2147ecec.ttf") format("truetype");
  font-weight: 400;
}

:root {
  --text-color-primary: #000;
  --text-color-secondary: #fff;
  --text-color-other: #907e82;
  --text-color-titles: #d41443;
  --text-color-labels: #ffa5ba;
  --background-color-primary: #fff;
  --background-color-secondary: #fff5f6;
  --background-color-other: #ffb8ca;
  --background-menu-container: rgba(255, 184, 202, .6);
  --background-mobile-menu: #e17992;
  --streak-color-mobile-menu: #e18298;
  --decorative-color: #ffa5ba;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--text-color-primary);
  background-color: var(--background-color-primary);
  margin: 0;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
}

body.no-scroll, body.modal-open {
  overflow: hidden;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.list {
  padding: 0;
  list-style: none;
}

.link {
  font-style: normal;
  text-decoration: none;
}

.title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--text-color-titles);
  font-family: Titan One;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
}

.label {
  text-align: center;
  text-transform: uppercase;
  color: var(--text-color-labels);
  font-family: Titan One;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.17;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
