}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(255, 245, 246, .6);
  position: fixed;
  top: 0%;
  left: 0%;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  transition: transform 250ms var(--timing-function);
  background-color: var(--background-color-primary);
  border-radius: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.modal__container {
  padding: 40px;
}

.modal__close-botton {
  width: 30px;
  height: 30px;
  background-color: var(--accent-color);
  transition: border 250ms var(--timing-function);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0%;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__close-botton:hover, .modal__close-botton:focus {
  border-color: var(--background-color-other);
}

.modal__icon-cross {
  width: 18px;
  height: 18px;
  fill: var(--text-color-primary);
}

.modal__title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--text-color-titles);
  margin-bottom: 30px;
  font-family: Titan One;
  font-size: 30px;
